'use strict';

(function() {
  // mobile menu
  const menuToggle = document.querySelector('.menu-toggle');
  const menu = document.querySelector('.menu__wrapper');
  
  menu.querySelectorAll('.menu__link').forEach((it) => {
    it.addEventListener('click', () => {
      if (menu.classList.contains('menu--opened')) {
        menuToggle.classList.remove('menu-toggle--opened');
        menu.classList.remove('menu--opened');
      }
    });
  });
  
  menuToggle.addEventListener('click', (evt) => {
    evt.preventDefault();
    menuToggle.classList.toggle('menu-toggle--opened');
    menu.classList.toggle('menu--opened');
  })

  // projects
  const prodjectBlocks = document.querySelectorAll('.projects__view');
  let selectedProject = null;
  prodjectBlocks.forEach((it) => {
    it.addEventListener('click', (evt) => {
      if (selectedProject) {
        selectedProject.classList.remove('projects__view--current');
      }
      evt.preventDefault();
      it.classList.toggle('projects__view--current');
      selectedProject = it;
    })
  });

  // certificate
  const createPopup = (src) => {
    const popup = document.createElement('div');
    const popupPic = document.createElement('img');
    const popupPicWrap = document.createElement('div');
    const popupClose = document.createElement('button');

    popup.classList.add('popup');
    popupPicWrap.classList.add('popup__pic-wrapper');
    popupPic.classList.add('popup__pic');
    popupPic.alt = `Изображение сертификата`;
    popupPic.src = `img/certificates/${src}.jpg`;
    popupClose.classList.add('popup__close');
    popupClose.setAttribute('aria-label', 'Закрыть попап');
    popupClose.type = 'button';
    
    popupClose.addEventListener('click', () => {
      popup.remove();
    });

    popupPicWrap.appendChild(popupPic);
    popup.appendChild(popupPicWrap);
    popup.appendChild(popupClose);
    document.querySelector('body').appendChild(popup);
  };

  const certificateLink = document.querySelectorAll('.education__list .button');
  certificateLink.forEach((it) => {
    it.addEventListener('click', (evt) => {
      evt.preventDefault();
      createPopup(it.dataset.src );
    })
  });

  
})();
